import Clipboard from 'clipboard';
import {setToken} from "../../plugins/auth";
import {initWebSocket} from "@/plugins/initWebsocket";

export default {
  layout: 'single-container',
  name: "Signup",
  data() {
    return {
      email: '',
      eye1: false,
      eye2: false,
      word1: '',
      code: '',
      daley: 0,
      deleyTimeId: null,
      sInviteCode: '',
      inviteCode: '',
      gift: 0,
      regesterSuc: false,
      isError: false,
    }
  },
  computed: {
    btnDisable() {
      var reg = new RegExp("^[a-zA-Z0-9]+([._\\-]*[A-Za-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$"); //正则表达式
      let wordReg = new RegExp('^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$');
      if (!this.email || !reg.test(this.email))
        return true;
      if (!wordReg.test(this.word1))
        return true;
      if (!this.code || this.code.length < 6)
        return true;
      return false;
    }
  },
  beforeDestroy() {
    clearInterval(this.deleyTimeId);
  },
  mounted() {
    // this.initCopy();
  },
  methods: {
    initCopy() {
      let btn = document.getElementById('copy');
      let clipboard = new Clipboard(btn);

      clipboard.on('success', () => {
        // this.$message.success(this.$t('message.lang_62'));
        this.$toast(this.$t('Mine32'));
      });

      clipboard.on('error', function (e) {
      });
    },
    onClose() {
      this.regesterSuc = false;
      this.$router.replace('/account/complete?step=1');
    },
    showPassword1() {
      this.eye1 = !this.eye1;
    },
    onBlur() {
      let wordReg = new RegExp('^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$');
      if (!wordReg.test(this.word1)) {
        this.isError = true;
        return;
      }
      this.isError = false;
    },
    async userRegister() {
      let data = await this.$axios.post('/v1/auth/UserRegister', {
        iSource: 1,
        iRegisterType: 2,
        sPassword: this.word1,
        sEmail: this.email,
        sEmailVerifyCode: this.code,
        sInviteCode: this.sInviteCode,
      })
      localStorage.setItem('Email', this.email);
      setToken(data.sSessionKey);
      this.$store.commit('SET_ISLOGIN', true)
      localStorage.setItem('inviteCode', data.sInviteCode)
      let user = await this.$axios.post('/v1/udc/GetUserExten', {})
      this.gift = parseInt(user.sAwardAmount);
      initWebSocket(data.iUserId);
      this.inviteCode = data.sInviteCode;
      this.regesterSuc = true;
      this.$nextTick(() => {
        this.initCopy();
      })
      // console.log('注册成功');
    },
    showPassword2() {
      this.eye2 = !this.eye2;
    },
    toRouter() {
      this.$router.push('/account/protocol');
    },
    async getCode() {
      var reg = new RegExp("^[a-zA-Z0-9]+([._\\-]*[A-Za-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$"); //正则表达式
      if (!this.email || !reg.test(this.email))
        return;
      await this.$axios.post('/v1/auth/CheckRegistered', {
        iRegisterType: 2,
        sEmail: this.email,
      })
      await this.$axios.post('/v1/auth/SendEmailVerifyCode', {
        iSource: 1,
        iSendMethod: 2,
        iVerifyBusi: 1,
        sEmail: this.email,
      })

      this.daley = 60;
      this.deleyTimeId = setInterval(() => {
        this.daley--;
        if (this.daley <= 0) {
          clearInterval(this.deleyTimeId);
        }
      }, 1000);
    }
  }
}
